*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

:root {
  --white-color: #fff;
  --text-color: #000;
  --text-secondary-color: #907e82;
  --title-color: #d41443;
  --label-color: #ffa5ba;
  --white-font-color: #fff;
  --accent-font-color: #d41443;
  --background-section-primary: #fff5f6;
  --background-section-white: #fff;
  --background-mobile-menu: #e17992;
  --background-primary-color: #ffb8ca;
  --background-secondary-color: #ffa5ba;
  --background-white: #fff;
  --card-color-first: #ffb8ca;
  --card-color-second: #f0d1a5;
  --card-color-third: #c2e297;
  --background-review: #fafafa;
  --decor-color: #e1e1e1;
  --review-accent: #c7133e;
  --review-common: #d9d9d9;
  --text-accent: #ffa5ba;
  --icon-color: #fff;
  --icon-background: #fea5bb;
  --icon-background-accent: #d41443;
  --background-button: #fff;
  --text-button: #d41443;
  --background-button-hover: #ecebeb;
  --reverse-background-button: #d41342;
  --reverse-text-button: #fff;
  --reverse-accent-button: #d41443;
  --indent: 32px;
  --items: 1;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 120;
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
}

body {
  letter-spacing: .04em;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.875;
}

.card-set {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.card-set .card-set__item {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
}

.button {
  letter-spacing: .03em;
  color: var(--text-button);
  background-color: var(--background-button);
  cursor: pointer;
  transition: color var(--transition), background-color var(--transition), box-shadow var(--transition);
  border: none;
  border-radius: 22px;
  padding: 11px 24px;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
}

.button:hover, .button:focus {
  background-color: var(--background-button-hover);
  box-shadow: 0px 3px 6px 1px var(--reverse-background-button);
}

.button--red {
  color: var(--reverse-text-button);
  background-color: var(--reverse-background-button);
}

.button--red:hover, .button--red:focus {
  color: var(--text-button);
  background-color: var(--background-button);
  box-shadow: 0px 3px 6px 1px var(--reverse-background-button);
}

.button--arrow {
  width: 40px;
  height: 40px;
  padding: 0;
}

.title {
  text-transform: uppercase;
  color: var(--title-color);
  font-family: Titan One, cursive;
}

@media screen and (max-width: 767px) {
  .title {
    letter-spacing: .04em;
    font-size: 34px;
    font-weight: 400;
    line-height: 1.14706;
  }
}

@media screen and (min-width: 768px) {
  .title {
    letter-spacing: .04em;
    font-size: 48px;
    font-weight: 400;
    line-height: 1.14583;
  }
}

@media screen and (min-width: 1200px) {
  .title {
    letter-spacing: .06em;
    font-size: 58px;
    font-weight: 400;
    line-height: 1.13793;
  }
}

.label-title {
  color: var(--label-color);
  text-transform: uppercase;
  font-family: Titan One, cursive;
}

@media screen and (max-width: 767px) {
  .label-title {
    letter-spacing: .03em;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.16667;
  }
}

@media screen and (min-width: 768px) {
