.link {
  cursor: pointer;
  transition: color var(--transition);
  display: block;
}

.link:hover, .link:focus {
  color: var(--accent-font-color);
}

.icon {
  fill: currentColor;
}

.modal {
  width: 528px;
  background-color: var(--background-primary-color);
  border: none;
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

.container {
