.menu-button {
  width: 22px;
  height: 22px;
  z-index: 110;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 1200px) {
  .menu-button {
    display: none;
  }
}

.burger-menu {
  width: 100%;
  height: 2px;
  background-color: var(--text-button);
  transition: transform var(--transition), background-color var(--transition);
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 0;
}

.burger-menu:before, .burger-menu:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--text-button);
  transition: transform var(--transition), background-color var(--transition);
  border-radius: 4px;
  position: absolute;
  left: 0;
}

.burger-menu:before {
  top: 8px;
}

.burger-menu:after {
  top: -8px;
}

.is-open .burger-menu {
  background-color: rgba(0, 0, 0, 0);
}

.is-open .burger-menu:before, .is-open .burger-menu:after {
  background-color: var(--reverse-text-button);
  top: 0;
}

.is-open .burger-menu:before {
  transform: rotate(45deg);
}

.is-open .burger-menu:after {
  transform: rotate(-45deg);
}

@keyframes bounce-up {
  from {
    transform: translateY(-5px);
  }

  to {
    transform: translateY(5px);
  }
}

.button-up {
  width: 40px;
  height: 40px;
  z-index: 10;
  color: var(--white-color);
  opacity: .7;
  background-color: var(--reverse-accent-button);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: bounce-up 1.2s linear infinite alternate both;
  display: flex;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

